
import Swiper from 'vux/src/components/swiper/swiper.vue'


export default {
name: 'MySwiper',
components: {
Swiper
},
props: {
bannerList: {
type: Array,
default: []
},
city: {
type: String,
default: ''
}
},
data () {
return {
currentIndex: 0
}
},
computed: {
swiperList () {
const bannerList = this.bannerList || []
return bannerList.length > 5 ? bannerList.slice(0, 5) : bannerList
}
},
methods: {
onIndexChange (index) {
this.currentIndex = index
}
}
}



