const wx = window.wx || {}

const wechat = {
  config (options = {}) {
    wx.config({
      // debug: true, // ,apialertpclogpc
      appId: options.appId, // 
      timestamp: options.timestamp, // 
      nonceStr: options.nonceStr, // 
      signature: options.signature, // 
      jsApiList: [
        'getLocation'
      ] // JS
    })
  },
  ready (cb) {
    wx.ready(function () {
      cb && cb()
    })
  },
  getLocation () {
    return new Promise((resolve, reject) => {
      wx.getLocation({
        type: 'wgs84', // wgs84gpsopenLocation'gcj02'
        success: function (res) {
          resolve({
            latitude: res.latitude, // 90 ~ -90
            longitude: res.longitude // 180 ~ -180
          })
        }
      })
    })
  }
}

export default wechat



