
import Loading from 'vux/src/components/loading/index.vue'

import api from '@/utils/api'
import { getLimitTitle } from '@/utils/utils'
import pageConfig from '@/utils/pageConfig'
import wechat from '@/utils/wechat'
import MySearch from '@/components/MySearch'
import MySwiper from '@/components/MySwiper'
import PostList from '@/components/PostList'


import ScrollNav from './pagelet/ScrollNav'














export default {
name: 'Home',
components: {
Loading,
MySearch,
MySwiper,
PostList,
ScrollNav
},
data () {
return {
searchTitle: '',
listData: [],
channelList: [],
bannerList: [],
loading: true,
page: 1,
city: ''
}
},
methods: {
setData (data = []) {
this.listData.splice(0)
this.listData = data
},
async loadMore (data = []) {
this.listData = this.listData.concat(data)
},
async handleSearch (value) {
this.searchTitle = value
this.page = 1
this.fetchData()
},
async fetchMoreData () {
try {
return await api.getHomeNewsList(this.page++, this.searchTitle)
} catch (err) {
console.error(err)
}
},
async fetchData () {
this.loading = true
try {
const data = await api.getHomeNewsList(this.page++, this.searchTitle)
this.setData(data)
} catch (err) {
console.error(err)
}
this.loading = false
},
async getChannelList () {
this.channelList = await api.getChannel()
},
async getBanners () {
const data = await api.getBanners()
this.bannerList = data.map(x => {
return {
url: `${pageConfig.newsDetailsLink}?aid=${x.id}`,
img: x.image,
title: getLimitTitle(x.title, 15)
}
})
},
async getLocation () {
const url = location.href.split('#')[0]
const data = await api.getWechatSign(url)

wechat.config(data)

wechat.ready(async () => {
const res = await wechat.getLocation()
const city = await api.getLocationCity(res.latitude, res.longitude)
this.city = city
})
}
},
mounted () {
this.getBanners()
this.getChannelList()
this.fetchData()
this.getLocation()
}
}



