
import Scroller from 'vux/src/components/scroller/index.vue'

import pageConfig from '@/utils/pageConfig'

let isDone = false

export default {
name: 'Home',
components: {
Scroller
},
props: {
channelList: {
type: Array,
default: []
}
},
data () {
return {
newslistLink: pageConfig.newslistLink
}
},
computed: {
navList () {
return this.channelList
},
navItemWidth () {
return `${(this.channelList.length * 146) / 28}rem`
}
},
watch: {
navList (val, oldVal) {
if (val.length > 0 && !isDone) {
this.$nextTick(() => {
isDone = true
const iconImgs = document.querySelectorAll('.p-img-icon img')
iconImgs.forEach(img => {
if (img.complete) {
const w = img.naturalWidth || img.width
img.style.width = w / 28 + 'rem'
} else {
img.onload = function () {
const w = this.naturalWidth || this.width
this.style.width = w / 28 + 'rem'
}
}
})
})
}
}
}
}



